<template>
  <div class="table">
    <!--  -->
    <el-row class='table-head'>
      <el-col :span='16'>
        <label></label>
        <el-input style='width:120px;' size='mini'></el-input>
        <label></label>
        <el-select v-model="value" placeholder="" size='mini' style='width:120px;'>
          <el-option
            key="1"
            label=""
            value="1">
          </el-option>
        </el-select>
        <el-button size='mini' type='primary'></el-button>
      </el-col>
      <!-- <el-col :span='8' class='table-head-btn'>
        <el-button size='mini' type='success' @click='changeshow'></el-button>
        <el-button size='mini' type='default'>Exel</el-button> -->
      <!-- </el-col>  -->
    </el-row>
    <!--  -->
    <el-table
      :data="tableData.list"
      stripe
      style="width: 100%">
      <el-table-column
        prop="id"
        width='100'
        label="ID">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="">
      </el-table-column>
      <el-table-column label=''>
        <template slot-scope="scope">
          {{scope.row.status === 1 ? '' : ''}}
        </template>
      </el-table-column>
      <el-table-column label=''>
        <template slot-scope="scope">
          {{scope.row.type === 1 ? '' : ''}}
        </template>
      </el-table-column>
      <el-table-column
        label="" 
        width="200">
        <template slot-scope="scope">
          {{getDate(scope.row.meta.regTime)}}
        </template>
      </el-table-column>
      <el-table-column
        width="300"
        label="">
        <template slot-scope="scope">
          <el-button @click='changStatus(scope.row.id, 1)' v-if='scope.row.status === 0' type='success' size='mini'></el-button>
          <el-button @click='changStatus(scope.row.id, 0)' v-if='scope.row.status === 1' type='warning' size='mini'></el-button>
          <el-button type='primary' size='mini' @click='changeshow(scope.row.id, true)'></el-button>
          <!-- <el-button type='default' size='mini'></el-button> -->
          <el-button type='danger' size='mini' @click='removeUser(scope.row.id)'></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="table-page">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="tableData.count">
      </el-pagination>
    </div>
    <!--  -->
    <transition name="fade">
      <userInfo :userId='selectId' v-if='show' @close='changeshow(false)'></userInfo>
    </transition>
  </div>
</template>

<script>
import userInfo from './userInfo';
import { getUserList, changeUserStatus, removeUser } from '@/js/http'; 
import axios from 'axios';
function getCookie(name){
  var strcookie = document.cookie;//cookie
  var arrcookie = strcookie.split("; ");//
  //
  for ( var i = 0; i < arrcookie.length; i++) {
    var arr = arrcookie[i].split("=");
    if (arr[0] == name){
      return arr[1];
    }
  }
  return "";
}

export default {
  components: { userInfo },
  mounted() {
    this.getList();
  },
  data() {
    return {
      show: false,
      value:'1',
      tableData:[],
      selectId: 0,
    }
  },
  methods: {
    async getList() { // 
      
      const res = await getUserList();

      // const res = await axios.get('http://127.0.0.1:7001/v1/admin/getUserList',{withCredentials: true})
      this.tableData = res.data.data;

    },
    getDate(str) { // 

      let date = new Date(str);
      date = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
      return date;

    },
    changStatus(id, status) { // 

      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await changeUserStatus({ id, status });
        this.$message({
          type: 'success',
          message: '!'
        });
        this.getList();
      });

    },
    removeUser(id) {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async (password) => {
        const res = await removeUser({ id, password });
        console.log(res);
      })
    },
    changeshow(id, val) {
      if(val) {
        this.selectId = id;
      }
      this.show = val;
    },
  },
}
</script>

<style lang='scss' scoped>
.table {
  &-head {
    padding: 10px 0 30px 0;
    label {
      font-size: 14px;
      color: #999;
    }
    &-btn {
      text-align: right;
    }
    .el-input {
      margin-right: 20px;
    }
    .el-select {
      margin-right: 20px;
    }
  }
  &-page {
    padding-top: 30px;
  }
}
</style>
