<template>
  <div class="add-admin">
    <transition name="slideDown">
      <div class="add-admin-box" v-if='show'>
        <h4>
          <span> / </span>
          <i class='el-icon-close' @click='close'></i>
        </h4>
        <!--  -->
        <div class="add-admin-form">
          <el-form size='mini' status-icon ref="ruleForm2" label-width="70px">
            <el-form-item label="ID" prop="pass">
              <span>{{ userInfo.id }}</span>
            </el-form-item>
            <el-form-item label="" prop="pass">
              <span>{{ type[userInfo.type] }}</span>
            </el-form-item>
            <el-form-item label="" prop="pass">
              <span>{{ getDate(userInfo.meta.regTime) || '' }}</span>
            </el-form-item>
            <el-form-item label="" prop="pass">
              <span>{{ userInfo.meta.prevLogTime ? getDate(userInfo.meta.prevLogTime) : '' }}</span>
            </el-form-item>
            <el-form-item label="" prop="pass">
              <el-input v-model="phone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="age">
              <el-row>
                <el-col :span='20' class='pwd'>
                  <span>****************</span>
                  <b> bibao123456</b>
                </el-col>
                <el-col :span='4'>
                  <el-button type='danger' style='width:100%'></el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
            </el-form-item>
          </el-form>
        </div>

      </div>
    </transition>
  </div>
</template>

<script>
import { getUserInfo } from '@/js/http';

export default {
  mounted() {
    this.show = true;
    this.getUserInfo();
  },
  props: [ 'userId' ],
  data() {
    return {
      show: false,
      type: ['',''],
      phone: null,
      userInfo: {},
    }
  },
  methods: {
    close() { // emit

      this.$emit('close',false)

    },
    getDate(str) { // 

      let date = new Date(str);
      date = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
      return date;

    },
    async getUserInfo() { // 

      const res = await getUserInfo({ id: this.userId });
      this.userInfo = res.data.data;
      this.phone = res.data.data.phone;

    }
  }
}
</script>

<style lang='scss' scoped>
@import '../../views/scss/public.scss';
@import './scss/addAdmin.scss';

</style>
